.home-page {
    text-align: center;
  }
  
  .news-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .news-item {
    width: 80%;
    margin-bottom: 20px;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    text-align: left;
  }
  
  .news-item h2 {
    font-size: 1.5em;
    margin-bottom: 10px;
  }
  
  .news-item p {
    font-size: 1em;
    margin-bottom: 10px;
  }
  
  .news-item a {
    color: #007bff;
    text-decoration: none;
  }
  
  .news-item a:hover {
    text-decoration: underline;
  }
  